const BaseUrl = 'http://gank.io/api'

export const Type = {
  all: 'all',
  boon: '',
  android: 'Android',
  ios: 'iOS',
  video: '',
  expand: '',
  front_end: ''
}

const Api = {
  // http://gank.io/api/random/data//
  random: (type = Type.all, num = 1) => {
    return fetch(`${BaseUrl}/random/data/${type}/${num}`).then(res => {
      return res.json()
    })
  },
  //  http://gank.io/api/day///
  date: (
    year = new Date().getFullYear(),
    month = new Date().getMonth() + 1,
    day = new Date().getDate()
  ) => {
    return fetch(`${BaseUrl}/day/${year}/${month}/${day}`).then(res => {
      return res.json()
    })
  },
  // : http://gank.io/api/data///
  category: (Type = Type.all, num = 10, page = 1) => {
    return fetch(`${BaseUrl}/data/${Type}/${num}/${page}`).then(res => {
      return res.json()
    })
  },
  // :https://gank.io/api/add2gank : POST
  submit: data => {
    if (!data) return
    return fetch(`${BaseUrl}/add2gank`, {
      method: 'POST',
      body: new FormData(data)
    }).then(res => res.status)
  },
  //  API
  search: (type = Type.all, count = 20, page = 1) => {
    return fetch(
      `${BaseUrl}/search/query/listview/category/${type}/count/${count}/page/${page}`
    ).then(res => {
      return res.json()
    })
  },
  // :
  publishedDates: () => {
    return fetch(`${BaseUrl}/day/history`).then(res => res.json())
  }
}

export default Api



